    color: #1c2224;
}

.custom-slick-next {
    position: absolute;
    top: 35%;
    right: 10px;
    display: block !important;
    transition: all 0.45s cubic-bezier(0.165, 0.84, 0.44, 1) 0s;
}
.custom-slick-prev {
    position: absolute;
    top: 35%;
    left: 10px;
    display: block !important;
    transition: all 0.45s cubic-bezier(0.165, 0.84, 0.44, 1) 0s;
    z-index: 800;
}

